/**
 *  0->Android 1->iP(hone|ad|od) 2->WindowsPhone 3->Windows 4->Mac 5->linux -1->others|unknown
 *  0->Chrome 1->IE 2->FireFox 3->Safari 4->Wechat 5->Q|QQZone 6->QQBrowser 7->UC 8->Weibo 9->ios Safari 10->Edge 11->Opera 12->IE11+
 */

/**
 * PC
 * @return {}
 */
export function getPCSystemInfo() {
    let ua = navigator.userAgent;
    let info = {};

    let clientStrings = [
            {s:'Windows 10', r:/(Windows 10.0|Windows NT 10.0)/},
            {s:'Windows 8.1', r:/(Windows 8.1|Windows NT 6.3)/},
            {s:'Windows 8', r:/(Windows 8|Windows NT 6.2)/},
            {s:'Windows 7', r:/(Windows 7|Windows NT 6.1)/},
            {s:'Windows Vista', r:/Windows NT 6.0/},
            {s:'Windows Server 2003', r:/Windows NT 5.2/},
            {s:'Windows XP', r:/(Windows NT 5.1|Windows XP)/},
            {s:'Windows 2000', r:/(Windows NT 5.0|Windows 2000)/},
            {s:'Windows ME', r:/(Win 9x 4.90|Windows ME)/},
            {s:'Windows 98', r:/(Windows 98|Win98)/},
            {s:'Windows 95', r:/(Windows 95|Win95|Windows_95)/},
            {s:'Windows NT 4.0', r:/(Windows NT 4.0|WinNT4.0|WinNT|Windows NT)/},
            {s:'Windows CE', r:/Windows CE/},
            {s:'Windows 3.11', r:/Win16/},
            {s:'Mac OS X', r:/Mac OS X/},
            {s:'Mac OS', r:/(MacPPC|MacIntel|Mac_PowerPC|Macintosh)/}
        ];

    let os;
    let len = clientStrings.length;
    for (let i=0; i<len; i++) {
        if(clientStrings[i].r.test(ua)) {
            os = clientStrings[i].s;
            break;
        }
    }

    if(/Windows/.test(os)) {
        info.sys = 3;
        info.sys_ver = /Windows (.*)/.exec(os)[1];
    } else if(os === "Mac OS X") {
        info.sys = 4;
        info.sys_ver = /Mac OS X (10[\.\_\d]+)/.exec(ua)[1];
    } else {
        info.sys = -1;
        info.sys_ver = -1;   
    }

    // browser
    let browser = navigator.appName;
    let version = '' + parseFloat(navigator.appVersion);
    let verOffset, ix;

    //Chrome
    if ((verOffset = ua.indexOf('Chrome')) != -1) {
        browser = 'Chrome';
        version = ua.substring(verOffset + 7);
        info.browser = 0;
    } else if ((verOffset = ua.indexOf('MSIE')) != -1) { //MSIE
        browser = 'Microsoft Internet Explorer';
        version = ua.substring(verOffset + 5);
        info.browser = 1;
    } else if ((verOffset = ua.indexOf('Firefox')) != -1) { //FireFox
        browser = 'Firefox';
        version = ua.substring(verOffset + 8);
        info.browser = 2;
    } else if ((verOffset = ua.indexOf('Safari')) != -1) { //Safari
        browser = 'Safari';
        version = ua.substring(verOffset + 7);
        if ((verOffset = ua.indexOf('Version')) != -1) {
            version = ua.substring(verOffset + 8);
        }
        info.browser = 3;
    } else if ((verOffset = ua.indexOf('Edge')) != -1) { //Edge
        browser = 'Microsoft Edge';
        version = ua.substring(verOffset + 5);
        info.browser = 10;
    } else if ((verOffset = ua.indexOf('Opera')) != -1) { //Opera
        browser = 'Opera';
        version = ua.substring(verOffset + 6);
        if ((verOffset = ua.indexOf('Version')) != -1) {
            version = ua.substring(verOffset + 8);
        }
        info.browser = 11;
    }else if (ua.indexOf('Trident/') != -1) {  // MSIE 11+
        browser = 'Microsoft Internet Explorer';
        version = ua.substring(ua.indexOf('rv:') + 3);
        info.browser = 12;
    } else {
        info.browser = -1;
    }  
  
    // trim the version string
    if ((ix = version.indexOf(';')) != -1) version = version.substring(0, ix);
    if ((ix = version.indexOf(' ')) != -1) version = version.substring(0, ix);
    if ((ix = version.indexOf(')')) != -1) version = version.substring(0, ix);

    info.browser_ver = parseInt(version);
    
    return info;
}

/**
 * 
 * @return {}
 */
export function getMobileSystemInfo() {
    let ua = navigator.userAgent;
    let info = {};

    //
    let nettype_pattern = /\bNetType\/\b(\w*)/i;
    if(nettype_pattern.exec(ua) != null) {
        if(nettype_pattern.exec(ua)[1] == "WIFI") {
            info.network = 1; //wifi
        } else {
            info.network = 2; //2g 3g 4g
        }
    } else {
        info.network = 0; //unknown
    }

    if(!info.examined) { //
        //os
        if(ua.indexOf("Android") > -1) {
            //Andorid
            info.sys = 0;
            info.sys_ver = parseFloat(ua.slice(ua.indexOf("Android") + 8));
        } else if(ua.indexOf("iPhone")>-1 || ua.indexOf("iPod")>-1 || ua.indexOf("iPad")>-1) {
            //ios
            info.sys = 1;
            let ios_pattern = /\bOS\s\b(\w+)/i;
            let ios_ver = ios_pattern.exec(ua)[1];
            info.sys_ver = ios_ver.split("_").join(".");
        } else if(ua.match(/Windows Phone/i)){
            //wp
            info.sys = 2;
            info.sys_ver = parseFloat(ua.slice(ua.indexOf("Windows Phone") + 14));
        }

        //
        let wechat_browser_pattern = /\bMicroMessenger\/\b(\S+)/; //
        let uc_browser_pattern = /\bUCBrowser\/\b(\S+)/; //UC
        let qq_browser_pattern = /\bMQQBrowser\/\b(\S+)/;  //QQ
        if(wechat_browser_pattern.exec(ua)) {
            //
            info.browser = 4;
            info.browser_ver = wechat_browser_pattern.exec(ua)[1];
        } else if(qq_browser_pattern.exec(ua)) {
            //MQQBrowser
            if(ua.indexOf("SQ") > -1) {
                //Q or QQzone
                info.browser = 5;
                info.browser_ver = qq_browser_pattern.exec(ua)[1];
            } else {
                //QQ
                info.browser = 6;
                info.browser_ver = qq_browser_pattern.exec(ua)[1];
            }
        } else if(uc_browser_pattern.exec(ua)) {
            //uc
            info.browser = 7;
            info.browser_ver = uc_browser_pattern.exec(ua)[1];
        } else if(ua.indexOf("weibo") > -1) {
            //weibo
            info.browser = 8;
            info.browser_ver = ua.split("__")[2];
        } else if(ua.indexOf("Safari") > -1 && ua.indexOf("Mobile") > -1) {
            //ios safari
            info.browser = 9;
            info.browser_ver = ua.slice(ua.indexOf("Safari") + 7);
        } else {
            info.browser = -1;
            info.browser_ver = "0.0";
        }

        //
        let device_pattern;
        if(info.sys === 0) {
            device_pattern = /\b[0-9]\;\s{1,}\b(.+)\s{1,}Build/i;
            let device_result = device_pattern.exec(ua); //
            if(device_result !== null && device_result.length >= 2) {
                info.device = device_result[1];
            } else {
                console.log("WARN-->" +"device!");
                info.device = "Android Device"; //android device 
            }

            if(info.device.indexOf(";") > -1) { //ua
                info.device = info.device.split("; ")[1];
            }
        } else if(info.sys === 1) {
            device_pattern = /\({1}(.+)\;{1}/i;
            info.device = device_pattern.exec(ua)[1];
        } else if(info.sys === 2) {
            info.device = "Windows Phone";
        }

        info.examined = true;
    }

    return info;
}