
/**
 * AVPlayer
 * debugavplayer.jslogDiv
 * @author yangq
 */
class AVLog {
    static get debug() {
        return this._debug;
    }

    static set debug(value) {
        this._debug = value;
    }

    static get logDivId() {
        return this._logDivId;
    }

    static set logDivId(id) {
        this._logDivId = id;
    }

    static print(...msg) {
        if(this._debug) {
            if(!!this._logDivId) {
                let logDiv = document.getElementById(this._logDivId);
                if(!!logDiv) {
                    let tempdiv = document.createElement("div");
                    tempdiv.innerHTML = "[" + new Date().toLocaleTimeString() + "] " + msg.join(" ");
                    logDiv.appendChild(tempdiv);
                }
            } else {
                console.log(...msg);
            }
        }        
    }
}

export default AVLog;